import * as React from "react"
import { Link } from "gatsby"
import { Search } from "lucide-react"

// Using Font Awesome via global CSS (e.g. included in layout.html or gatsby-browser.js)

const BLOG_CATEGORIES = [
  "Guest Experience",
  "Host Stories",
  "Hosting 101",
  "Hosting Tools",
  "Insights & Trends",
  "Managing Property",
  "Pricing Strategies",
  "Scaling Tips",
]

const slugifyCategory = name =>
  name
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "")

const Header = () => {
  const [isBlogOpen, setIsBlogOpen] = React.useState(false)
  return (
    <header className="sticky top-0 z-50 bg-white/90 border-b border-[var(--color-editorial-border)] backdrop-blur">
      <div className="max-w-[1200px] mx-auto px-4 pt-8 pb-6">
        <div className="flex items-center justify-between gap-8">
          <Link to="/" className="no-underline flex-shrink-0">
            <span
              className="text-[var(--color-editorial-black)]"
              style={{ fontSize: "2.75rem", letterSpacing: "0.04em", fontWeight: 600 }}
            >
              HostHustle
            </span>
          </Link>

          <nav
            className="hidden md:flex items-center justify-center flex-1"
            style={{ fontSize: "1rem", letterSpacing: "0.24em" }}
          >
            <div className="flex items-center gap-8 text-[var(--color-editorial-black)] font-semibold">
              <Link to="/" className="hover:text-[var(--color-editorial-accent)] transition-colors">HOME</Link>
              <Link to="/tools" className="hover:text-[var(--color-editorial-accent)] transition-colors">TOOLS</Link>
              <Link to="/courses" className="hover:text-[var(--color-editorial-accent)] transition-colors">COURSES</Link>
              {/*<Link to="/health" className="hover:text-[var(--color-editorial-accent)] transition-colors">HEALTH</Link>
              <Link to="/design" className="hover:text-[var(--color-editorial-accent)] transition-colors">DESIGN</Link> */}

              <div
                className="relative"
                onMouseEnter={() => setIsBlogOpen(true)}
                onMouseLeave={() => setIsBlogOpen(false)}
              >
                <Link
                  to="/blog"
                  className="hover:text-[var(--color-editorial-accent)] transition-colors"
                >
                  BLOGS
                </Link>

                {isBlogOpen && (
                  <div
                    className="absolute left-1/2 mt-3 bg-white border border-[var(--color-editorial-border)] rounded editorial-shadow"
                    style={{ transform: "translateX(-50%)", zIndex: 60 }}
                  >
                    <div className="px-4 py-3">
                      <div className="flex items-center gap-6">
                        {BLOG_CATEGORIES.map(name => {
                          const slug = `/blog/category/${slugifyCategory(name)}/`
                          return (
                            <Link
                              key={slug}
                              to={slug}
                              className="text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)] transition-colors whitespace-nowrap font-semibold"
                              style={{ fontSize: "0.75rem", letterSpacing: "0.08em" }}
                            >
                              {name.toUpperCase()}
                            </Link>
                          )
                        })}
                      </div>
                    </div>
                  </div>
                )}
              </div>

              <Link to="/contact" className="hover:text-[var(--color-editorial-accent)] transition-colors">CONTACT</Link>
            </div>
          </nav>

          <div className="flex items-center gap-6 flex-shrink-0">
            <div className="hidden md:flex items-center gap-4 text-[var(--color-editorial-black)]">
              <button type="button" className="cursor-pointer" aria-label="Facebook">
                <i className="fab fa-facebook-f" />
              </button>
              <button type="button" className="cursor-pointer" aria-label="Twitter">
                <i className="fab fa-twitter" />
              </button>
              <button type="button" className="cursor-pointer" aria-label="Instagram">
                <i className="fab fa-instagram" />
              </button>
            </div>

            <div
              className="hidden sm:flex items-center gap-2 pl-4 pr-6 py-2 rounded-full border border-[var(--color-editorial-border)] bg-white/80 text-[var(--color-editorial-grey)] shadow-sm"
              style={{ fontSize: "0.8rem" }}
            >
              <span className="opacity-70">Search...</span>
            </div>

            <button
              type="button"
              className="w-10 h-10 rounded-full bg-[var(--color-editorial-black)] flex items-center justify-center text-white"
              aria-label="Search"
            >
              <Search className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header
